import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export class Navbar extends Component {


    render() {
        return (
            <>
            <nav className="navbar">
                <div className="nav__container d-flex">
                    <div className = "nav__logo__container d-flex"><a href="#" className = "nav__logo">ReactNews</a></div>
                    <ul className="nav__list d-flex">
                        <li className="nav__item"><Link to="/" className="nav__link">Top Headlines</Link></li>
                        <li className="nav__item"><Link to="/business" className="nav__link">Business</Link></li>
                        <li className="nav__item"><Link to="/entertainment" className="nav__link">Entertainment</Link></li>
                        <li className="nav__item"><Link to="/health" className="nav__link">Health</Link></li>
                        <li className="nav__item"><Link to="/science" className="nav__link">Science</Link></li>
                        <li className="nav__item"><Link to="/sports" className="nav__link">Sports</Link></li>
                        <li className="nav__item"><Link to="/technology" className="nav__link">Technology</Link></li>
                    </ul>
                    <div className="burger">
                <i class="ri-menu-line" id = "menuBtn"></i>
                </div>
                </div>
        
            </nav>

        </>
        )
    }
}

export default Navbar
