import React, { Component } from 'react';
import PropTypes from 'prop-types';
import DateTime from './DateTime';
import news from './assets/news.jpg';
const date = DateTime();



export default class NewsBody extends Component {
    static defaultProps = {
    
    }
    static propTypes = {
        category:PropTypes.string
    }
    
    constructor(props) {
        super(props);
        this.state = {
            isLoaded: false,
            articles: []
        }
    }
    async componentDidMount() {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=44d38b406f414dc5ad52669e36aa2bc0`;
        let data = await fetch(url);
        let dataJson = await data.json();
        this.setState({
            isLoaded: true,
            articles: dataJson.articles
        })
    }


    render() {
        let { heading } = this.props;
        

        return (
            <>
                <section className="news__container d-flex">
                    <div className="heading d-flex">
                        <h1 className="news__type">{heading}</h1>
                        <p className="date">{date}</p>
                    </div>
                    <div className="news__body d-flex">
                        {this.state.articles.map(element => {
                            return (
                                <div className="card">
                                    <div className="news__img">
                                        
                                        <img src={ImageReturn(element.urlToImage)} alt="thumbnail" className="thumbnail" />
                                    </div>
                                    <div className="news__content">
                                        <h3 className="title">{element.title}</h3>
                                        <a rel = "noreferrer" target ="_blank" href={element.url} className="readmore">Read More</a>
                                    </div>
                                </div>
                            )
                        })}

                    </div>
                </section>
            </>
        )
    }
}

function ImageReturn(img){
    if(img==null){
        return news;
    }
    else{
        return img;
    }
}